<template>
  <div class="container">
    <main class="content-wrapper">
      <!-- Camera section -->
      <section class="card camera-section">
        <div class="card-header">
          <h2>Camera Real-time Detection</h2>
        </div>
        <div class="card-body">
          <div class="camera-container">
            <div class="camera-preview-wrapper">
              <video 
                id="cameraPreview" 
                class="camera-preview" 
                autoplay 
                playsinline
                ref="videoRef"
              ></video>
              <canvas 
                id="detectionCanvas" 
                class="detection-canvas"
                ref="canvasRef"
              ></canvas>
            </div>
            <div class="camera-controls">
              <button 
                class="btn btn-primary start-camera-btn" 
                @click="startCamera"
                :disabled="isCameraActive || isLoading"
              >
                <i class="fas fa-play"></i> Start Camera
              </button>
              <button 
                class="btn btn-danger stop-camera-btn" 
                @click="stopCamera"
                :disabled="!isCameraActive"
              >
                <i class="fas fa-stop"></i> Stop Camera
              </button>
              <button 
                class="btn btn-secondary toggle-detection-btn" 
                @click="toggleDetection"
                :disabled="!isCameraActive"
              >
                <i v-if="isDetecting" class="fas fa-pause"></i>
                <i v-if="!isDetecting" class="fas fa-play"></i>
                {{ isDetecting ? 'Pause Detection' : 'Start Detection' }}
              </button>
            </div>
          </div>
        </div>
      </section>

      <!-- Results section -->
      <section class="card results-section">
        <div class="card-header">
          <h2>Detection Results</h2>
        </div>
        <div class="card-body">
          <div id="detectionResults" class="results-content">
            <div v-if="isLoading" class="status-indicator loading">
              <i class="fas fa-circle-notch fa-spin"></i>
              <p>Detecting, please wait...</p>
            </div>
            <div v-if="errorMessage" class="status-indicator error">
              <i class="fas fa-exclamation-circle"></i>
              <p>{{ errorMessage }}</p>
            </div>
            <div v-if="detections.length > 0 && !isLoading && !errorMessage" class="detections-list">
              <p class="results-summary">Detected <span class="count">{{ detections.length }}</span> cucumbers:</p>
              <ul>
                <li v-for="(obj, index) in detections" :key="index" class="detection-item">
                  <div class="detection-class">{{ obj.class }}</div>
                  <div class="detection-meta">
                    <span class="confidence">Confidence: {{ obj.confidence.toFixed(2) }}</span>
                    <span class="position">Position: {{ formatBox(obj.box) }}</span>
                  </div>
                </li>
              </ul>
            </div>
            <div v-if="!isCameraActive && !isLoading && !errorMessage && detections.length === 0" class="status-indicator info">
              <i class="fas fa-info-circle"></i>
              <p>Please click "Start Camera" to begin real-time detection</p>
            </div>
          </div>
        </div>
      </section>
    </main>

    <footer class="page-footer">
      <p>PA-YOLO Cucumber Detection System &copy; 2025</p>
    </footer>
  </div>
</template>

<script setup>
import { ref, onUnmounted, nextTick } from 'vue';
import { io } from 'socket.io-client';

// ---------------- 摄像头状态 ----------------
const videoRef = ref(null);
const canvasRef = ref(null);
const isCameraActive = ref(false);
const isDetecting = ref(false);
const detectionInterval = ref(null);
const streamRef = ref(null);

// ---------------- 检测结果 ----------------
const detections = ref([]);
const isLoading = ref(false);
const errorMessage = ref('');

// ---------------- SocketIO ----------------
const socket = io('http://127.0.0.1:3000'); // 替换为你的后端地址

socket.on('connect', () => console.log('Connected to SocketIO server'));
socket.on('detection_result', (data) => {
  detections.value = data.detections || [];
  drawDetectionBoxes(detections.value, data.annotated_image);
  isLoading.value = false;
});
socket.on('detection_error', (data) => {
  errorMessage.value = data.error || 'Unknown detection error';
  isLoading.value = false;
});

// ---------------- 启动摄像头 ----------------
const startCamera = async () => {
  try {
    streamRef.value = await navigator.mediaDevices.getUserMedia({ 
      video: { width: { ideal: 1280 }, height: { ideal: 720 }, facingMode: 'environment' } 
    });
    await nextTick();
    if (videoRef.value) {
      videoRef.value.srcObject = streamRef.value;
      videoRef.value.onloadedmetadata = () => {
        if (canvasRef.value) {
          canvasRef.value.width = videoRef.value.videoWidth;
          canvasRef.value.height = videoRef.value.videoHeight;
        }
      };
    }
    isCameraActive.value = true;
    errorMessage.value = '';
  } catch (error) {
    errorMessage.value = `Camera start failed: ${error.message}`;
    isCameraActive.value = false;
  }
};

// ---------------- 停止摄像头 ----------------
const stopCamera = () => {
  if (streamRef.value) {
    streamRef.value.getTracks().forEach(track => track.stop());
    streamRef.value = null;
  }
  if (videoRef.value) videoRef.value.srcObject = null;
  toggleDetection(false);
  isCameraActive.value = false;
  detections.value = [];
};

// ---------------- 切换检测 ----------------
const toggleDetection = (forceState = null) => {
  const newState = forceState !== null ? forceState : !isDetecting.value;
  if (newState && !isDetecting.value) {
    isDetecting.value = true;
    detectionInterval.value = setInterval(sendFrameToServer, 1000); // 每秒发送一帧，可调整
  } else if (!newState && isDetecting.value) {
    isDetecting.value = false;
    if (detectionInterval.value) {
      clearInterval(detectionInterval.value);
      detectionInterval.value = null;
    }
  }
};

// ---------------- 发送摄像头帧到服务器（高性能 Blob 方式） ----------------
const sendFrameToServer = () => {
  if (!isCameraActive.value || !videoRef.value || !canvasRef.value || isLoading.value) return;
  try {
    isLoading.value = true;
    const ctx = canvasRef.value.getContext('2d');
    ctx.drawImage(videoRef.value, 0, 0, canvasRef.value.width, canvasRef.value.height);

    // Blob 二进制发送
    canvasRef.value.toBlob(blob => {
      if (blob) socket.emit('camera_frame', blob);
    }, 'image/jpeg', 0.8);

  } catch (err) {
    errorMessage.value = `Detection error: ${err.message}`;
    isLoading.value = false;
  }
};

// ---------------- 绘制检测框 ----------------
const drawDetectionBoxes = (detectionsList, annotatedImage = null) => {
  if (!canvasRef.value || !videoRef.value) return;
  const ctx = canvasRef.value.getContext('2d');

  if (annotatedImage) {
    const img = new Image();
    img.onload = () => ctx.drawImage(img, 0, 0, canvasRef.value.width, canvasRef.value.height);
    img.src = 'data:image/jpeg;base64,' + annotatedImage;
    return;
  }

  ctx.drawImage(videoRef.value, 0, 0, canvasRef.value.width, canvasRef.value.height);
  detectionsList.forEach(obj => {
    const [x1, y1, x2, y2] = obj.box;
    ctx.strokeStyle = '#3b82f6';
    ctx.lineWidth = 2;
    ctx.strokeRect(x1, y1, x2 - x1, y2 - y1);
    ctx.fillStyle = '#3b82f6';
    const label = `${obj.class} (${obj.confidence.toFixed(2)})`;
    const textMetrics = ctx.measureText(label);
    ctx.fillRect(x1, y1 - 20, textMetrics.width + 10, 20);
    ctx.fillStyle = 'white';
    ctx.font = '14px Arial';
    ctx.fillText(label, x1 + 5, y1 - 5);
  });
};

const formatBox = (box) => box.map(coord => coord.toFixed(2)).join(', ');

// 清理
onUnmounted(() => {
  stopCamera();
  if (socket.connected) socket.disconnect();
});
</script>






