<template>
  <div class="container">
    
    
    <main class="content-wrapper">
      <!-- Detection mode selection -->
      
      
      <!-- Folder upload section -->
      <section class="card folder-section">
        <div class="card-header">
          <h2>Batch Folder Upload</h2>
        </div>
        <div class="card-body">
          <div class="folder-upload-container">
            <label for="folderInput" class="folder-label">
              <span class="folder-icon">
                <i class="fas fa-folder-open"></i>
              </span>
              <span class="folder-text" v-if="folderFiles.length === 0">Click or drag folder here to upload</span>
              <div v-if="folderFiles.length > 0" class="folder-files-info">
                <p>{{ folderFiles.length }} image files selected</p>
                <div class="files-list">
                  <div v-for="(file, index) in folderFiles" :key="index" class="file-item">
                    <i class="fas fa-file-image"></i>
                    <span class="file-name">{{ file.name }}</span>
                    <button 
                      class="remove-file-btn" 
                      @click.stop="removeFile(index)"
                      title="Remove file"
                    >
                      <i class="fas fa-times"></i>
                    </button>
                  </div>
                </div>
              </div>
              <input 
                type="file" 
                id="folderInput" 
                webkitdirectory 
                directory 
                accept="image/*" 
                @change="handleFolderChange"
                class="folder-input"
              >
            </label>
            
            <div class="folder-actions">
              <button 
                class="btn btn-primary process-folder-btn" 
                @click="processFolder"
                :disabled="folderFiles.length === 0 || isLoading || isProcessing"
              >
                <span v-if="!isProcessing">Batch Detect ({{ folderFiles.length }})</span>
                <span v-if="isProcessing">
                  <i class="fas fa-spinner fa-spin"></i> Processing ({{ processedCount }}/{{ folderFiles.length }})
                </span>
              </button>
              <button 
                class="btn btn-secondary clear-folder-btn" 
                @click="clearFolder"
                :disabled="folderFiles.length === 0 || isProcessing"
              >
                <i class="fas fa-trash"></i> Clear List
              </button>
            </div>
          </div>
          
          <!-- Batch processing progress -->
          <div v-if="isProcessing" class="processing-progress">
            <div class="progress-bar" :style="{ width: progressPercentage + '%' }"></div>
          </div>
        </div>
      </section>

      <!-- Results section -->
      <section class="card results-section">
        <div class="card-header">
          <h2>Detection Results</h2>
        </div>
        <div class="card-body">
          <div id="detectionResults" class="results-content">
            <!-- Loading state -->
            <div v-if="isLoading" class="status-indicator loading">
              <i class="fas fa-circle-notch fa-spin"></i>
              <p>Detecting, please wait...</p>
            </div>
            
            <!-- Error message -->
            <div v-if="errorMessage" class="status-indicator error">
              <i class="fas fa-exclamation-circle"></i>
              <p>{{ errorMessage }}</p>
            </div>
            
            <!-- Batch detection results list -->
            <div v-if="batchResults.length > 0 && !isLoading && !errorMessage" class="batch-results-list">
              <p class="results-summary">Batch Detection Results ({{ processedCount }}/{{ folderFiles.length }})：</p>
              <div class="batch-results-container">
                <div v-for="(result, index) in batchResults" :key="index" class="batch-result-item">
                  <div class="result-header">
                    <h4>{{ result.filename }}</h4>
                    <span class="result-count">{{ result.detections.length }} cucumbers detected</span>
                  </div>
                  <div class="result-content">
                    <img 
                      :src="result.annotatedUrl || result.previewUrl" 
                      class="batch-result-preview" 
                      alt="Detection result preview"
                    >
                    <ul class="batch-detections-list">
                      <li v-for="(obj, i) in result.detections" :key="i" class="batch-detection-item">
                        <span class="detection-class">{{ obj.class }}</span>
                        <span class="detection-confidence">Confidence: {{ obj.confidence.toFixed(2) }}</span>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
            
            <!-- No folder selected prompt -->
            <div v-if="folderFiles.length === 0 && !isLoading && !errorMessage && batchResults.length === 0" class="status-indicator info">
              <i class="fas fa-info-circle"></i>
              <p>Please select a folder containing images for batch detection</p>
            </div>
          </div>
        </div>
      </section>
    </main>
    
    <footer class="page-footer">
      <p>PA-YOLO Cucumber Detection System &copy; 2025</p>
    </footer>
  </div>
</template>


<script setup>
import { ref } from 'vue';

// 文件夹批量上传相关状态
const folderFiles = ref([]);
const isProcessing = ref(false);
const processedCount = ref(0);
const progressPercentage = ref(0);
const batchResults = ref([]);
const isLoading = ref(false);
const errorMessage = ref('');

// 处理文件夹选择
const handleFolderChange = (e) => {
  const files = Array.from(e.target.files);
  if (files.length > 0) {
    // 过滤出图片文件
    const imageFiles = files.filter(file => file.type.startsWith('image/'));
    
    // 为每个文件生成预览
    imageFiles.forEach(file => {
      const reader = new FileReader();
      reader.onload = (event) => {
        file.previewUrl = event.target.result;
        folderFiles.value.push(file);
      };
      reader.readAsDataURL(file);
    });
  }
  // 重置输入以允许重新选择同一文件夹
  e.target.value = '';
};

// 移除单个文件
const removeFile = (index) => {
  folderFiles.value.splice(index, 1);
};

// 清空文件夹列表
const clearFolder = () => {
  folderFiles.value = [];
  batchResults.value = [];
  processedCount.value = 0;
  progressPercentage.value = 0;
};

// 处理文件夹批量检测
const processFolder = async () => {
  if (folderFiles.value.length === 0) return;
  
  // 重置状态
  isProcessing.value = true;
  isLoading.value = true;
  errorMessage.value = '';
  batchResults.value = [];
  processedCount.value = 0;
  progressPercentage.value = 0;
  
  try {
    // 逐个处理文件
    for (let i = 0; i < folderFiles.value.length; i++) {
      const file = folderFiles.value[i];
      
      // 跳过非图片文件
      if (!file.type.startsWith('image/')) {
        processedCount.value++;
        progressPercentage.value = Math.round((processedCount.value / folderFiles.value.length) * 100);
        continue;
      }
      
      // 创建FormData并发送请求
      const formData = new FormData();
      formData.append('image', file);
      
      const response = await fetch('https://123.56.203.202/detect', {
        method: 'POST',
        body: formData
      });
      
      if (!response.ok) {
        throw new Error(`处理第 ${i+1} 个文件时出错，HTTP状态码：${response.status}`);
      }
      
      const data = await response.json();
      
      if (data.error) {
        throw new Error(`处理第 ${i+1} 个文件时出错：${data.error}`);
      }
      
      // 保存结果
      batchResults.value.push({
        filename: file.name,
        detections: data.detections || [],
        previewUrl: file.previewUrl,
        annotatedUrl: data.annotated_image ? `data:image/jpg;base64,${data.annotated_image}` : null
      });
      
      // 更新进度
      processedCount.value++;
      progressPercentage.value = Math.round((processedCount.value / folderFiles.value.length) * 100);
      
      // 每处理一个文件后短暂延迟，避免请求过于密集
      if (i < folderFiles.value.length - 1) {
        await new Promise(resolve => setTimeout(resolve, 500));
      }
    }
    
  } catch (error) {
    errorMessage.value = `批量处理错误：${error.message}`;
    console.error('批量检测失败：', error);
  } finally {
    isProcessing.value = false;
    isLoading.value = false;
  }
};
</script>

